import { useRef, useState } from 'react'
import Wizard from '@components/wizard'
import QuizDetails from './QuizDetails'
import QuizQuestions from './QuizQuestions'

// ** Import BreadCrumbs From Component
import BreadCrumbs from '@components/breadcrumbs'

const AddQuiz = () => {
  const [stepper, setStepper] = useState(null)
  const ref = useRef(null)

  const steps = [
    {
      id: 'quiz-details',
      title: 'Quiz Details',
      subtitle: 'Enter Quiz Details.',
      content: <QuizDetails stepper={stepper} />
    },
    {
      id: 'step-questions-eng',
      title: 'Questions',
      subtitle: 'Add Questions',
      content: <QuizQuestions stepper={stepper} />
    }
  ]

  return (
    <div className="horizontal-wizard">
       <BreadCrumbs breadCrumbTitle='Quiz' breadCrumbActive='Add' />
      <Wizard instance={(el) => setStepper(el)} ref={ref} steps={steps} />
    </div>
  )
}

export default AddQuiz
