  const store = useSelector(state => state.quiz)
  const {langs, lang_count, addedQuizID} = store
    const dispatch = useDispatch()
  const { register, errors, handleSubmit, control, trigger } = useForm({
    // resolver: yupResolver(QuizDetailsSchema)
  })
  const increaseCount = () => {
    // if (count === 2) { 
    //   console.log("error")
    // } else {
      setCount(count + 1) 
  }
  const preventMinus = (e) => {
    console.log(e)
    if (e.key === '-' || e.key === '.' || e.key === '+' || e.key === 'e') {
        e.preventDefault()
    }
}

  // const en = "1"
  const deleteForm = (e) => {
    e.preventDefault()
    console.log('count', count)
    setCount(count - 1)
    // e.target.closest('form').remove()
    console.log(count)
  }

  // useEffect(() => {
