import { Fragment, useState } from 'react'
// import { useHistory } from 'react-router-dom'
import classnames from 'classnames'
import { isObjEmpty} from '@utils'
import { useForm } from 'react-hook-form'
// import { addQuestion } from '../../Quiz/store/action'
import {getengquestions} from "../../Quiz/store/action"
import Repeater from '@components/repeater'
import { Label, FormGroup, Row, Col, Button, Form, Input } from 'reactstrap'
import { Plus, ArrowRight } from 'react-feather'
import { useSelector, useDispatch } from 'react-redux'

const AddQuestions = ({ stepper }) => {
  const { register, errors, handleSubmit, trigger } = useForm()
  const dispatch = useDispatch()
  // const history = useHistory()
  const store = useSelector(state => state.quiz)
  const { lang_id, langs, addedQuizID} = store
  const onSubmit = () => {
    trigger()
    if (isObjEmpty(errors)) {
      stepper.next()
    }
  }
  const [quest] = useState([{quest:"test"}, {quest:"test2"}])
  console.log("id", lang_id)
  console.log(langs)
  const [count, setCount] = useState(1)
  const increaseCount = () => {
    if (count === 5) return
    setCount(count + 1)
  }

  // const langOptions = [
  //   { value: '1', label: 'English' },
  //   { value: '2', label: 'Arabic' },
  //   {value: '3',  label: 'tamil'}
  // ]

  const submitQuestion = (id) => {
    trigger().then((res) => {
      if (res) {
        const param = {
          quiz_id: addedQuizID,
          translation_quiz_questions: [
            {
              master_language_id: lang_id,
              translation_quiz_question: document.getElementById(`question-${id}`).value
            }
          ],
          quiz_question_duration: '30',
          quiz_question_sno: id,
          quiz_question_answers: [
            {
              translation_quiz_answers:[
                {
                  // master_language_id:document.getElementById('quiz_lang').value,
                  master_language_id:lang_id,
                  translation_quiz_answer: document.getElementById(`answer1-${id}`).value
                }
              ],
            quiz_answer_sno: '1',
           quiz_answer_correct:
           document.querySelector(`input[name="correct-answer-${id}"]:checked`)
           .value === '1'
           ? 1
           : 0
           },
          {
            translation_quiz_answers:[
              {
                master_language_id:lang_id,
                translation_quiz_answer: document.getElementById(`answer2-${id}`).value
              }
            ],
          quiz_answer_sno: '2',
         quiz_answer_correct:
         document.querySelector(`input[name="correct-answer-${id}"]:checked`)
         .value === '1'
         ? 1
         : 0
         },
         {
          translation_quiz_answers:[
            {
              master_language_id:lang_id,
              translation_quiz_answer: document.getElementById(`answer3-${id}`).value
            }
          ],
        quiz_answer_sno: '3',
       quiz_answer_correct:
       document.querySelector(`input[name="correct-answer-${id}"]:checked`)
       .value === '1'
       ? 1
       : 0
       },
       {
        translation_quiz_answers:[
          {
            master_language_id:lang_id,
            translation_quiz_answer: document.getElementById(`answer4-${id}`).value
          }
        ],
      quiz_answer_sno: '4',
     quiz_answer_correct:
     document.querySelector(`input[name="correct-answer-${id}"]:checked`)
     .value === '1'
     ? 1
     : 0
     }
    ]}
    
        // dispatch(addQuestions(param))
        console.log(param)
        console.log(document.getElementById('quiz_lang').value)
      }
    })
  }
  return (
    <Fragment>
      <div className="content-header">
        <h5 className="mb-0">Questions</h5>
        <small>Enter Questions.</small>
      </div>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Repeater count={count}>
          {(i) => (
            <li key={i} className="list-unstyled">
              <Row>
                <FormGroup tag={Col} md="12">
                  <Label className="form-label" for={`question-${i}`}>
                    Quiz question
                  </Label>
                  <Input
                    type="text"
                    id={`question-${i}`}
                    name={`question-${i}`}
                    placeholder="Who is Lionel Messi?"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`question-${i}`]
                    })}
                  />
                </FormGroup>
              </Row>
              <Row>
                <FormGroup tag={Col} md="8">
                  <Label className="form-label" for={`answer1-${i}`}>
                    Answer 1
                  </Label>
                  <Input
                    type="text"
                    name={`answer1-${i}`}
                    id={`answer1-${i}`}
                    placeholder="Cricketer"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer1-${i}`]
                    })}
                  />
                </FormGroup>
                <FormGroup tag={Col} md="4">
                  <div className="d-flex height-95-per align-items-end">
                    <FormGroup check inline>
                      <Label check>
                        <Input
                          type="radio"
                          name={`correct-answer-${i}`}
                          defaulChecked
                          value="1"
                        />
                        Correct Answer
                      </Label>
                    </FormGroup>
                  </div>
                </FormGroup>
              </Row>
              <Row>
                <FormGroup tag={Col} md="8">
                  <Label className="form-label" for={`answer2-${i}`}>
                    Answer2
                  </Label>
                  <Input
                    type="text"
                    name={`answer2-${i}`}
                    id={`answer2-${i}`}
                    placeholder="Footballer"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer2-${i}`]
                    })}
                  />
                </FormGroup>
                <FormGroup tag={Col} md="4">
                  <div className="d-flex height-95-per align-items-end">
                    <FormGroup check inline>
                      <Label check>
                        <Input
                          type="radio"
                          name={`correct-answer-${i}`}
                          value="2"
                        />
                        Correct Answer
                      </Label>
                    </FormGroup>
                  </div>
                </FormGroup>
              </Row>
              <Row>
                <FormGroup tag={Col} md="8">
                  <Label className="form-label" for={`answer3-${i}`}>
                    Answer 3
                  </Label>
                  <Input
                    type="text"
                    name={`answer3-${i}`}
                    id={`answer3-${i}`}
                    placeholder="VolleyBaller"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer3-${i}`]
                    })}
                  />
                </FormGroup>
                <FormGroup tag={Col} md="4">
                  <div className="d-flex height-95-per align-items-end">
                    <FormGroup check inline>
                      <Label check>
                        <Input
                          type="radio"
                          name={`correct-answer-${i}`}
                          value="3"
                        />
                        Correct Answer
                      </Label>
                    </FormGroup>
                  </div>
                </FormGroup>
              </Row>
              <Row>
                <FormGroup tag={Col} md="8">
                  <Label className="form-label" for={`answer4-${i}`}>
                    Answer 4
                  </Label>
                  <Input
                    type="text"
                    name={`answer4-${i}`}
                    id={`answer4-${i}`}
                    placeholder="BasketBaller"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer4-${i}`]
                    })}
                  />
                </FormGroup>
                <FormGroup tag={Col} md="4">
                  <div className="d-flex height-95-per align-items-end">
                    <FormGroup check inline>
                      <Label check>
                        <Input
                          type="radio"
                          name={`correct-answer-${i}`}
                          value="4"
                        />
                        Correct Answer
                      </Label>
                    </FormGroup>
                  </div>
                </FormGroup>
              </Row>
              <Row>
                <FormGroup tag={Col} md="6">
                  <div className="d-flex justify-content-end">
                    <Button.Ripple
                      type="button"
                      color="primary"
                      className="btn-next"
                      onClick={() => submitQuestion(i)}
                    >
                      <span className="align-middle d-sm-inline-block d-none">
                        Submit Questions
                      </span>
                    </Button.Ripple>
                  </div>
                </FormGroup>
              </Row>
            </li>
          )}
        </Repeater>
        <div className="d-flex justify-content-between">
          {/* {count <= 4 && ( */}
          {count <= 30 && (
            <Button.Ripple
              className="btn-icon"
              color="primary"
              onClick={increaseCount}
            >
              <Plus size={14} />
              <span className="align-middle ml-25">Add New Question</span>
            </Button.Ripple>
          )}
        </div>
        <div className="d-flex justify-content-end">
          {/* <Button.Ripple
            color="secondary"
            className="btn-prev"
            outline
            disabled
          >
            <ArrowLeft
              size={14}
              className="align-middle mr-sm-25 mr-0"
            ></ArrowLeft>
            <span className="align-middle d-sm-inline-block d-none">
              Previous
            </span>
          </Button.Ripple> */}
          <Button.Ripple 
          onClick={() => {
              stepper.next()
              console.log(document.getElementById(`question-${0}`).value)
            //   setQuest([
            //       {quest1:document.getElementById(`question-${0}`).value}
                //   {quest2:document.getElementById(`question-${1}`).value}
            //  {quest3:document.getElementById(`question-${2}`).value},
            // {quest4:document.getElementById(`question-${3}`).value}
        // ])
                dispatch(getengquestions(quest))
                console.log(quest)
                //   dispatch(getengquestions("ok"))
            }} 
          color="primary" 
          className="btn-next">
            <span className="align-middle d-sm-inline-block d-none">Next</span>
            <ArrowRight
              size={14}
              className="align-middle ml-sm-25 ml-0"
            ></ArrowRight>
          </Button.Ripple>
        </div>
      </Form>
    </Fragment>
  )
}

export default AddQuestions


